import { Component, OnInit } from '@angular/core';
import { Router} from '@angular/router';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
@Component({
  selector: 'app-booking-plot',
  templateUrl: './booking-plot.page.html',
  styleUrls: ['./booking-plot.page.scss'],
})
export class BookingPlotPage implements OnInit {
  filterTerm!:string
  public booking_attr:any=[
    {title:'Plot No'},
    {title:'type'},
    // {title:'Property'},
    // {title:'Date'},
    {title:'Location'},
    {title:'Size(Marla)'},
    {title:'Size(Sq.ft)'},
    {title:'Price'},
    {title:'Status'},
    {title:'Book'},
    {title:'Delete'}
  ]
  public Get_Customer:any;
  public length:any;
  public booking_details : any ={p_id:'', plot:'',plot_no:'', area_marla:'',  area_sqf:'' , p_location: '' , type:'', price:'', img:'', description:'' }

  constructor(public apiCall:ApicallService, private route: Router , public global: GlobalService) { }

  ngOnInit() {
    this.get_Plot();
  }
  
    // Get function of plot_data
    async get_Plot() {
      this.apiCall.api_getPlots();
      await this.global.Plots.subscribe(user => {
        console.log(user);
        this.booking_details = user;
       });
     }

    // Delete function of plot_data
   async Delete_Plot(p_id) {
    const x = {p_id:p_id}
    await this.apiCall.api_delete_plot(x);
    this.get_Plot();
    console.log(p_id);
  }


  add_booking(booking){
    console.log(booking);
    this.route.navigate(['add-booking'], { state: { data: booking } })
  }

  Property(){
    this.route.navigate(['property'])
  }
  booking(){
    this.route.navigate(['booking'])
  }
  Home(){
    this.route.navigate(['home'])
  }
  booking_plot(){
    this.route.navigate(['booking-plot'])
  }
  Refresh(){
    this.apiCall.api_getPlots();
  }

}
